import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { selectCurrentUser } from '@store/auth/auth.selectors';
import { AppStateInterface } from '@store/app/app-state.interface';
import { getProfileAction } from '@store/profile/profile.actions';
import { User } from '@shared-types/user.interface';

@Component({
  selector: 'app-user-profile',
  templateUrl: 'user-profile.component.html',
  styleUrls: ['user-profile.component.scss'],
})
export class UserProfileComponent implements OnInit {
  user$: Observable<User | null> = this.store.select(selectCurrentUser);
  userSlug!: string;
  userId: string | undefined;
  userProfile: any;

  constructor(
    private store: Store<AppStateInterface>,
    private route: ActivatedRoute,
    private router: Router,
  ) {}

  ngOnInit() {
    this.initializeListeners();
    this.user$.subscribe((user) => {
      this.userId = user?.userId;
    });
  }

  initializeListeners() {
    this.route.params.subscribe((params: Params) => {
      this.userSlug = params['userSlug'];
      this.fetchUserProfile();
    });
  }

  fetchUserProfile() {
    this.store.dispatch(getProfileAction({ slug: this.userSlug }));
  }
}
